const Event = ({ dayNum, transRef }) => {

    const [windowProperties] = useContext(WindowPropertiesContext);
    const [eventProperties, setDay, setGroup, setDayAndGroup] = useContext(CardStatesContext);

    let isDesktop = windowProperties.width > 800;

    let firstGroupLabelStyle = 'violet';
    let secondGroupLabelStyle = 'violet-blue';
    let thirdGroupLabelStyle = 'blue';

    if (!isDesktop) {
        if (eventProperties.group === 0) {
            firstGroupLabelStyle = 'white pointer violet-bg events-header';
            secondGroupLabelStyle += ' pointer';
            thirdGroupLabelStyle += ' pointer';
        } else if (eventProperties.group === 1) {
            secondGroupLabelStyle = 'white pointer grad-bg events-header';
            firstGroupLabelStyle += ' pointer';
            thirdGroupLabelStyle += ' pointer';
        } else {
            thirdGroupLabelStyle = 'white pointer blue-bg events-header';
            firstGroupLabelStyle += ' pointer';
            secondGroupLabelStyle += ' pointer';
        }
    }

    const getDayText = (day) => {
        if (day === 0) {
            return <h1 className="padded-container container text-centered" style={{marginBottom: '.75em'}}><br />27  2019</h1>
        } else if (day === 1) {
            return <h1 className="padded-container container text-centered" style={{marginBottom: '.75em'}}><br />2  2019</h1>
        } else if (day === 2) {
            return <h1 className="padded-container container text-centered" style={{marginBottom: '.75em'}}><br />3  2019</h1>
        }
    }

    const handleRightSwipe = () => {
        if (eventProperties.group > 0) {
            setGroup(eventProperties.group - 1);
        } else {
            if (dayNum > 0) {
                setDayAndGroup(eventProperties.day - 1, 2);
            }
        }
    }

    const handleLeftSwipe = () => {
        if (eventProperties.group < 2) {
            setGroup(eventProperties.group + 1);
        } else {
            if (dayNum < 2) {
                setDayAndGroup(eventProperties.day + 1, 0);
            }
        }
    }

    const swipeHandler = useSwipeable({ onSwipedRight: (eventData) => handleRightSwipe(), onSwipedLeft: (eventData) => handleLeftSwipe() });
    
    const dayTransitions = useTransition(eventProperties.day === dayNum ? events[dayNum].lectures : [], item => item.id, {
        ref: transRef,
        unique: true,
        trail: 1,
        config: (item, state) => {
            switch (state) {
              case "enter": return { duration: 200 }
              case "leave": return { duration: 200 }
            }
        },
        from: { transform: 'scale(0)', position: 'absolute' },
        enter: { transform: 'scale(1)', position: 'static' },
        leave: { transform: 'scale(0)' }
    });

    // const groupTransitions = useTransition(eventProperties.group, item => item, {
    //     config: { mass: 1, tension: 170, friction: 25 },
    //     from: { opacity: 1, transform: 'translate3d(100%,0,0)' },  // 'translate3d(-100%,0,0)'
    //     enter: { opacity: 1, transform: 'translate3d(0%,0,0)' },
    //     leave: { 
    //         opacity: 0,
    //         transform: 'translate3d(-100%,0,0)', //: 'translate3d(100%,0,0)',
    //         position: 'absolute',
    //     },
    // });

    // const [toggle, set] = useState(false)

    const groupTransitions = useTransition(eventProperties.group, null, {
        config: { mass: 1, tension: 170, friction: 25 },
        config: config.stiff,
        from: { opacity: 0, transform: eventProperties.prevGroup < eventProperties.group ? 'translate3d(100%,0,0)' : 'translate3d(-100%,0,0)' },
        enter: { opacity: 1, transform: 'translate3d(0%,0,0)' },
        leave: { opacity: 0, transform: eventProperties.prevGroup < eventProperties.group ? 'translate3d(-100%,0,0)' : 'translate3d(100%,0,0)', position: 'absolute' },
    })

    // return groupTransitions.map(({ group, key, props }) => 
    // group !== -1
    // ? <animated.div key={key} style={props}><EventCard
    // key={group} 
    // timeIndex={item.id}
    // eventName={item.data[group].eventName}
    // lecturerName={item.data[group].lecturerName}
    // time={item.data[group].time}
    // groupNum={group} /></animated.div>
    // : <div className="hide" />
    // )

//     const [toggle, set] = useState(1)
// const transitions = useTransition(eventProperties.group, null, {
// from: { position: 'absolute', opacity: 0 },
// enter: { opacity: 1 },
// leave: { opacity: 0 },
// })

// return groupTransitions.map(({ item, key, props }) => 
// <animated.div>{item}</animated.div>
// )

    return (
        <div className="relative" {...swipeHandler}>
            
            { dayTransitions.map(({ item, key, props }) => {
                const externalItem = item;
            return <animated.div key={key} style={props}>
                {key === '0' ? <div>{getDayText(dayNum)}<div className="flex ev-cards-headers ev-padding-label">
                <div className={"group-label ev-right-margin text-centered " + firstGroupLabelStyle} onClick={() => setGroup(0)}>1 </div>
                <div className={"group-label ev-right-margin text-centered " + secondGroupLabelStyle} onClick={() => setGroup(1)}>2 </div>
                <div className={"group-label ev-right-margin text-centered " + thirdGroupLabelStyle} onClick={() => setGroup(2)}>3 </div>
            </div></div> : <div className="hide" />}
                <div key={item.id} className="ev-cards ev-font-padding">
                    { isDesktop ? item.data.map((ev, groupNum) => {
                        return <EventCard
                            key={groupNum} 
                            timeIndex={item.id}
                            eventName={ev.eventName}
                            lecturerName={ev.lecturerName}
                            time={ev.time}
                            groupNum={groupNum} />
                    }) : groupTransitions.map(({ item, key, props }) => 
                        <animated.div key={key} style={props}><EventCard
                            key={item} 
                            timeIndex={externalItem.id}
                            eventName={externalItem.data[item].eventName}
                            lecturerName={externalItem.data[item].lecturerName}
                            time={externalItem.data[item].time}
                            groupNum={item} />
                        </animated.div>) }
                    {/* { item.data.map((ev, groupNum) => {
                                return (isDesktop || eventProperties.group === groupNum) ? <EventCard
                                    key={groupNum} 
                                    timeIndex={item.id}
                                    eventName={ev.eventName}
                                    lecturerName={ev.lecturerName}
                                    time={ev.time}
                                    groupNum={groupNum} /> : <div key={groupNum} className="hide" />
                            }) } */}
                </div>
                        </animated.div>}) }
        </div>
    );
}

export default Event;