export const CardStatesProvider = props => {

    const [eventProperties, setEventProperties] = useState({ day: 0, group: 0, prevGroup: 0, openedCard: { groupNum: -1, cardNum: -1 },
        checkedCards: [new Array(events[0].lectures.length).fill(-1),
        new Array(events[1].lectures.length).fill(-1),
        new Array(events[2].lectures.length).fill(-1)] });

    const setDay = (selectedDay) => {
        setEventProperties({ ...eventProperties, day: eventProperties.day === selectedDay ? -1 : selectedDay, openedCard: { groupNum: -1, cardNum: -1 } });
    }

    const setGroup = (selectedGroup) => {
        setEventProperties({ ...eventProperties, prevGroup: eventProperties.group, group: selectedGroup, openedCard: { groupNum: -1, cardNum: -1 } });
    }

    const setDayAndGroup = (selectedDay, selectedGroup) => {
        setEventProperties({ ...eventProperties, prevGroup: eventProperties.group, day: selectedDay, group: selectedGroup });
    }

    const setOpenedCard = (grNum, cNum) => {
        setEventProperties({ ...eventProperties, openedCard: { groupNum: grNum, cardNum: cNum } });
    }

    const setCheckedCard = (grNum, cNum) => {
        const arr = eventProperties.checkedCards.slice();
        arr[eventProperties.day][cNum] = arr[eventProperties.day][cNum] === grNum ? -1 : grNum;
        setEventProperties({ ...eventProperties, checkedCards: arr });
    }

    return <CardStatesContext.Provider value={[eventProperties, setDay, setGroup, setDayAndGroup, setOpenedCard, setCheckedCard]}>
        { props.children }
    </CardStatesContext.Provider>

}